body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	background-image: linear-gradient(90deg, #ff008d, #4523ff);
	background-repeat: repeat-y;
	background-size: contain;
	overflow: auto;
}

.App {
	text-align: center;
	overflow: hidden;
	margin: 0;
	padding: 0;
}

.auth-container {
	overflow: auto;
	background-color: white;
	max-width: 500px;
	padding: 40px;
	margin: auto;

	margin-top: 50px;
}

@media screen and (max-width: 500px) {
	.auth-container {
		width: 90%;
	}
}

.login-main-div {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 255px;
	overflow: auto;
}
.login-title {
	font-weight: 800;
	text-align: left;
	color: #fd008d;
}

.inputfield {
	border-style: none;
	padding: 10px;
}

.inputfield:focus {
	outline: none;
}

.input-button-login {
	width: 80px;
	padding: 4px;
	border-style: none;
	background-color: #fd008d;
	border-radius: 7px;
	color: white;
	-webkit-border-radius: 7px;
	-moz-border-radius: 7px;
	-ms-border-radius: 7px;
	-o-border-radius: 7px;
}

.input-button-login:focus {
	outline: none;
}
.button-div {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.signup-main-div {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 456px;

	overflow: auto;
}
.signup-title {
	font-weight: 800;
	text-align: left;
	color: #4622fe;
}

.inputfield {
	border-style: none;
	padding: 10px;
}

.inputfield:focus {
	outline: none;
}

.input-button-signup {
	width: 80px;
	padding: 4px;
	border-style: none;
	background-color: #4622fe;
	border-radius: 7px;
	color: white;
	-webkit-border-radius: 7px;
	-moz-border-radius: 7px;
	-ms-border-radius: 7px;
	-o-border-radius: 7px;
}

.input-button-signup:focus {
	outline: none;
}

.button-div {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.banner-main-div {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: auto;
	margin-top: 150px;
	padding: 20px;
	color: white;
	text-shadow: 0px 0px 20px #242424;
}
.welcome {
	text-align: right;
	margin: 0;
}
.company-name {
	font-weight: 800;
}
.slogan {
	margin: 0;
	text-align: left;
	border-bottom: 0.5px solid grey;
}
.banner-button-div {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 25px;
}

.buttons {
	margin: 10px;
	width: 82px;
	height: 35px;
	font-weight: 600;
	border-style: none;
	border-radius: 50px;
	box-shadow: 11px 10px 8px 0px #18181887;
	-webkit-border-radius: 50px;
	-moz-border-radius: 50px;
	-ms-border-radius: 50px;
	-o-border-radius: 50px;
}

.buttons:focus {
	outline: none;
}

@media screen and (max-width: 500px) {
	.banner-main-div {
		text-align: center;
	}

	.welcome {
		text-align: center;
	}

	.slogan {
		text-align: center;
	}
}

.navbar {
	flex-direction: row;
	justify-content: space-between;
	background-color: #374755;
	color: white;
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
	z-index: 1;
}

.brand-name {
	color: white;
}

.nav-button {
	background: none;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 30px;
	padding-right: 30px;
	border-style: none;
	color: white;
}

.nav-button:focus {
	outline: none;
	background-color: #1a2d3e;
}

.search {
	width: 50px;
	padding-left: 23px;
	margin-top: 8px;
	margin-right: 15px;
	height: 27px;
	border-style: none;

	border-radius: 19px;
	transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
}

.search:focus {
	width: 200px;
	padding-left: 23px;
	margin-top: 8px;
	margin-right: 15px;
	height: 27px;
	border-style: none;
	background-color: #ffffff;
	border-radius: 19px;
	transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
}

.nav-button:hover {
	background-color: #1a2d3e;
}

.navbar-buttons {
	flex-direction: row-reverse;
}

.logout {
	color: #fe9696;
	font-weight: 600;
}

.logout:hover {
	background-color: #3e1a1a;
	color: white;
}

.header-title {
	padding-left: 30px;
	padding-top: 9px;
}

@media screen and (min-width: 500px) {
}

.edit-main-div {
	margin-top: 80px;
	padding: 20px;
	display: grid;
	grid-gap: 2rem;
	gap: 2rem;
	grid-row-gap: 2rem;
	row-gap: 2rem;
	grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
}

.add-new-card {
	cursor: pointer;
	width: 250px;
	height: 350px;
	margin: auto;
	background-color: #efefef;
	padding-top: 55px;
	color: grey;
	border-radius: 10px;
	transition: all 0.3s;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	-ms-transition: all 0.3s;
	box-shadow: 9px 8px 8px 1px #00000038;
}

.add-card-title {
	font-size: 1.3em;
	font-weight: 800;
}

.card-main-div {
	height: 355px;
	transition: all 0.4s;
	-webkit-transition: all 0.4s;
	-moz-transition: all 0.4s;
	-ms-transition: all 0.4s;
	-o-transition: all 0.4s;
}

.card {
	width: 250px;
	height: 350px;
	margin: auto;
	box-shadow: 9px 8px 8px 1px #00000038;
	border-radius: 10px;
	transition: all 0.3s;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	-ms-transition: all 0.3s;
	-o-transition: all 0.3s;
}

.img-div {
	overflow: auto;
	overflow-y: hidden;
	overflow-x: hidden;
	border-radius: 10px 10px 0px 0px;
	-webkit-border-radius: 10px 10px 0px 0px;
	-moz-border-radius: 10px 10px 0px 0px;
	-ms-border-radius: 10px 10px 0px 0px;
	-o-border-radius: 10px 10px 0px 0px;
}

.card-img {
	width: 250px;
	height: 318px;
	transition: all 0.3s;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	-ms-transition: all 0.3s;
	-o-transition: all 0.3s;
}
.card-img:hover {
	transform: scale(1.1);
	-webkit-transform: scale(1.1);
	-moz-transform: scale(1.1);
	-ms-transform: scale(1.1);
	-o-transform: scale(1.1);
}

/* Like Classes */

.like-div {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
}
.like-button {
	background-color: transparent;
	border-style: none;
	font-size: 2.1em;
	color: #b7b7b7;
	height: 0px;
	margin-top: -51px;
}

.like-button:focus {
	outline: none;
}
.like-message {
	margin: 0;
	padding: 7px;
	color: grey;
}

.edit-card-button {
	border-radius: 0px 0px 0px 10px;
	background-color: #28a745;
	-webkit-border-radius: 0px 0px 0px 10px;
	-moz-border-radius: 0px 0px 0px 10px;
	-ms-border-radius: 0px 0px 0px 10px;
	-o-border-radius: 0px 0px 0px 10px;
}

.edit-card-button:focus {
	background-color: #419644;
}
.delete-card-button {
	border-radius: 0px 0px 10px 0px;

	background-color: #dc3545;
	-webkit-border-radius: 0px 0px 10px 0px;
	-moz-border-radius: 0px 0px 10px 0px;
	-ms-border-radius: 0px 0px 10px 0px;
	-o-border-radius: 0px 0px 10px 0px;
}

.delete-card-button:focus {
	background-color: #bd3329;
}

.control-buttons {
	border-style: none;
	height: 30px;
	width: 50%;
	color: white;
}

.control-buttons:focus {
	outline: none;
}

.btn-primary {
	margin-right: 10px;
	width: 65px;
}


.admin-details-main-div {
	display: flex;
	flex-direction: column;
	margin-top: 45px;
	padding: 20px;
}

.table-div {
	overflow-x: scroll;
}

.table {
	background-color: white;
	border-radius: 0px 0px 15px 15px;
	-webkit-border-radius: 0px 0px 15px 15px;
	-moz-border-radius: 0px 0px 15px 15px;
	-ms-border-radius: 0px 0px 15px 15px;
	-o-border-radius: 0px 0px 15px 15px;
	overflow-x: hidden;
}

.nav-link {
	background-color: #ffffff5c;

	margin-right: 2px;
}

.cards-div {
	padding: 50px;
	display: grid;
	margin-top: -20px;
	grid-gap: 2rem;
	gap: 2rem;
	grid-row-gap: 2rem;
	row-gap: 2rem;
	grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
	overflow-y: hidden;
}

.title {
	color: white;
	font-weight: 800;
	padding-left: 50px;
	text-align: left;
	margin: 0;
	text-shadow: 0px 0px 20px #242424;
}

.cards-super-div {
	margin-top: 55px;
	padding-top: 30px;
}

@media screen and (max-width: 600px) {
	.title {
		text-align: center;
		padding: 0;
	}
}

.details-main-div {
	margin: auto;
	margin-top: 90px;
	padding: 20px;
	margin-top: -10px;
	padding: 20px;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	text-align: left;
	background-color: #ffffff8c;
	width: 90%;
	border-radius: 16px 0px 16px 16px;
	-webkit-border-radius: 16px 0px 16px 16px;
	-moz-border-radius: 16px 0px 16px 16px;
	-ms-border-radius: 16px 0px 16px 16px;
	-o-border-radius: 16px 0px 16px 16px;
}
.super-div {
	padding-bottom: 20px;
}

.default {
	text-align: left;
}

.name {
	font-weight: 800;
	font-size: 2.5em;
	width: 400px;
}
.detail-div {
	text-align: left;
}
.like {
	font-weight: 600;
	margin-top: 16px;
}
.price {
	font-weight: 800;
	font-size: 1.5em;
}
.details {
	max-width: 398px;
}

.label {
	font-weight: 100;
}

.card-image {
	width: 316px;
	border: 5px solid white;
	box-shadow: 13px 11px 13px 0px #0000004d;
}

.close-button {
	border-style: none;
	color: white;
	height: 35px;
	margin-bottom: 1.1%;
	width: 40px;

	background-color: #fd003f;

	border-radius: 10px 10px 0px 0px;

	padding: 7px;
	font-weight: 700;

	margin-right: 5%;
}

.btn-success {
	background-color: #4722fc;
	border-color: #4722fc;
}

@media screen and (max-width: 800px) {
	.close-button {
		margin-bottom: 0%;
	}
	.details-main-div {
		flex-direction: column;
		text-align: center;
	}
	.detail-div {
		margin-top: 35px;
		border-top: 1px solid #ffffff73;
		padding-top: 35px;
	}

	.details {
		margin: auto;
		margin-bottom: 10px;
	}
}

.close-button:focus {
	outline: none;
}

.super-div {
	margin-top: 80px;
	text-align: right;
}

@media screen and (max-width: 400px) {
	.card-image {
		width: 280px;
	}

	.name {
		width: 278px;
	}
}

